import React, { useEffect } from 'react';
import * as d3 from 'd3';

const Viz = (props) => {
  useEffect(() => {
   draw(props)
 }, [2])
  return (
  <div id="explain-d3-graph">
    <svg width="500" height="500">
      <g class="links"></g>
      <g class="nodes"></g>
    </svg>
  </div>)
}

const draw = (props) => {
    const width = 500,
          height = 500;

    //let nodes = props.nodes;
    //let links = props.edges;
    var nodes = [
        {name: 'Addd'},
        {name: 'Bdsdsdsdss'},
        {name: 'Cddsdssdd'},
        {name: 'Ddsdsdsdssd'},
        {name: 'Esdadsa'},
        {name: 'Fdsddssdds'},
        {name: 'Gsdds'},
        {name: 'Hsddsdsd'},
    ]
       
       
    var links = [
         {source: 0, target: 1},
         {source: 0, target: 2},
         {source: 0, target: 3},
         {source: 0, target: 4},
         {source: 0, target: 5},
         {source: 0, target: 6},
         {source: 1, target: 7},
         {source: 2, target: 7},
         {source: 3, target: 7},
         {source: 4, target: 7},
         {source: 5, target: 7},
         {source: 6, target: 7},
    ]

    d3.forceSimulation(nodes)
        .force('charge', d3.forceManyBody().strength(-200))
        .force('center', d3.forceCenter(width / 2, height / 2))
        .force('link', d3.forceLink().distance(200).links(links))
        .on('tick', ticked);

    function updateLinks() {
        var u = d3.select('.links')
            .selectAll('line')
            .data(links)

        u.enter()
            .append('line')
            .merge(u)
            .attr('x1', function(d) {
                return d.source.x
            })
            .attr('y1', function(d) {
                return d.source.y
            })
            .attr('x2', function(d) {
                return d.target.x
            })
            .attr('y2', function(d) {
                return d.target.y
            })
            .attr("stroke", "#ccc");

        u.exit().remove()
    }

    function updateNodes() {

        let u = d3.select('.nodes')
            .selectAll('text')
            .data(nodes)
            .style("fill", "#69b3a2");


        u.enter()
            .append('text')
            .text(function(d) {
                return d.name
            })
            .merge(u)
            .attr('x', function(d) {
                return d.x
            })
            .attr('y', function(d) {
                return d.y
            })
            .attr('dy', function(d) {
                return 5
            })
            .attr("text-anchor", "middle")
            .attr("font-size", 10)
            .attr("font-family", "Helvetica Neue")
        nodes[0].x = 30;
        nodes[0].y = height / 2;
        nodes[0].fixed = true;
        nodes[nodes.length - 1].x = width - 50;
        nodes[nodes.length - 1].y = height / 2;
        nodes[nodes.length - 1].fixed = true;

        u.exit().remove()
    }

    function ticked() {
        updateLinks()
        updateNodes()
    }

}
export default Viz
